@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.HomePage {
    box-sizing: border-box;
    height: 100vh;
    position: relative;
}

.HomePage h1 {
    box-sizing: border-box;
    margin: 0;
    padding-top: 2rem;
    width: 100%;
    position: absolute;
    top: 2rem;
    color: #fff;
    font-size: 3rem;
}

.gridPage {
    display: grid;
    grid-template-columns: 33.33% 33.33% 33.33%;
    height: 100vh;
}

.gridItem {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.cinepolis-color {
    background-color: var(--cinepolis-color);
}

.vips-color {
    background-color: var(--vips-color);
}

.viajes-color {
    background-color: var(--miescape-color);
}

.logo {
    width: 50%;
    max-width: 50%;
}

@media screen and (max-width: 767px) {
    .HomePage h1 {
        background-color: var(--red-rv-t);
        position: relative;
        top: 0;
        margin: 0;
        padding: 1rem 0;
        font-size: 1.8rem;
        height: 10vh;
    }
    .gridPage {
        grid-template-columns: auto;
        grid-template-rows: 33.33% 33.33% 33.33%;
        height: 90vh;
    }
}

@media screen and (max-width: 375px){
    .HomePage h1 {
        font-size: 1.6rem;
    }
}

:root{
  --cinepolis-color: #0070c7;
  --vips-color: #fe9800;
  --miescape-color: #5ad602;
  --green-two: #74ae86;
  --red-rv: #cc233c;
  --red-rv-t: #cc3b36;
  --gray-first: #a2a2a2;
  --gray-second: #ecece9;
  --text-gray: #9a999e;
}

html, body {
  font-family: 'Ubuntu', sans-serif;
}

a {
  text-decoration: none;
}

hr {
  max-width: 360px;
  margin-top: 2rem;
}

::-webkit-input-placeholder {
  color: #9a999e;
  color: var(--text-gray);
}

::-ms-input-placeholder {
  color: #9a999e;
  color: var(--text-gray);
}

::placeholder {
  color: #9a999e;
  color: var(--text-gray);
}

label {
  color: #9a999e;
  color: var(--text-gray);
}



input[type=text], input[type=tel], input[type=password], input[type=number], input[type=email] {
  border: 1px solid rgba(236,236,233,0.3);
  min-width: 300px;
  padding: 1rem 1.5rem;
  font-size: 1rem;
  font-family: 'Ubuntu', sans-serif;
  margin: 0.5rem 0;
}

input[type=number] {
  min-width: 80px;
  width: 80px;
  margin-right: 10px;
}

button, .btn {
  padding: 1rem 1.5rem;
  border: none;
  border-radius: 1.5rem;
  font-family: "Ubuntu", sans-serif;
  font-size: 1rem;
}

button.send {
  background: #74ae86;
  background: var(--green-two);
  color: #fff;
  width: 100%;
  max-width: 280px;
}

footer hr {
  border: 1px solid #fe9800;
  border: 1px solid var(--vips-color);
  max-width: 50%;
}

footer {
  color: #949393;
  margin-top: 5rem;
}

footer a {
  color: #949393;
}

footer a i {
  color: #fe9800;
  color: var(--vips-color);
}

.cinepolis footer {
  background: #283244;
  padding-top: 1rem;
}

.outline {
  display: block;
  margin: 0 auto;
  border-radius: 1.5rem;
  padding: 1rem 1.5rem;
  border: 2px solid #a2a2a2;
  border: 2px solid var(--gray-first);
  color: #a2a2a2;
  color: var(--gray-first);
  font-size: 1rem;
  width: 100%;
  max-width: 280px;
  margin-top: 2rem;
}

